.mui-header{box-sizing:border-box;padding-left:15px;padding-right:15px;background-color:#fff;position:relative}.mui-header:after{content:" ";position:absolute;left:0;bottom:0;width:100%;height:1px;border-bottom:1px solid #D9D9D9;color:#D9D9D9}@media screen and (-webkit-min-device-pixel-ratio: 2){.mui-header:after{-webkit-transform-origin:0 100%;transform-origin:0 100%;-webkit-transform:scaleY(0.5);transform:scaleY(0.5);border-top:1px solid #bebebe;color:#bebebe}}.mui-header .mui-header-left{-webkit-flex-grow:1;-ms-flex-positive:1;-webkit-box-flex:1;flex-grow:1;text-align:left}.mui-header .mui-header-middle{-webkit-flex-grow:2;-ms-flex-positive:2;-webkit-box-flex:2;flex-grow:2;text-align:center}.mui-header .mui-header-right{-webkit-flex-grow:1;-ms-flex-positive:1;-webkit-box-flex:1;flex-grow:1;text-align:right}.mui-header.mui-flexbox--unsupport{height:44px}.mui-header.mui-flexbox--unsupport .mui-header-left{float:left;width:25%}.mui-header.mui-flexbox--unsupport .mui-header-middle{float:left;width:50%}.mui-header.mui-flexbox--unsupport .mui-header-right{float:left;width:25%}.mui-header-back{color:#0092D8;line-height:44px}.mui-header-back:before{content:" ";display:inline-block;border-color:#0092D8;border-style:solid;height:14px;width:14px;border-width:2px 2px 0 0;-webkit-transform:rotate(-135deg);transform:rotate(-135deg);position:relative;top:-2px;top:2px;margin-left:.3em}h1.mui-header-title{line-height:44px;font-weight:normal;color:#333;font-size:16px;margin:0}



